@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor_producto {
    border-radius: .8rem;

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .header_imagen {

        img {
            border-radius: .8rem .8rem 0 0;
            
            @include m.tablet {
                border-radius: .8rem 0 0 .8rem;
                height: 100%;
            }
        }
    }

}


.producto {
    padding: 2rem;

    @include m.tablet {
        padding: 3rem;
    }

    blockquote {
        margin: 0;
        margin-bottom: 1rem;
        text-transform: uppercase;
        color: v.$azulGrisaceo;
        font-size: 1.4rem;
        letter-spacing: .5rem;

        @include m.tablet {
            margin-bottom: 2rem;
        }
    }

    .titulo_producto {
        color: v.$azulOscuro;
        text-align: left;
        line-height: 3rem;

        @include m.tablet {
            font-size: 3.5rem;
            line-height: 4rem;
        }
    }

    .precio_producto {
        display: flex;
        gap: 3rem;
        margin: 2rem 0;
        
        p {
            margin: 0;
        }
        .actual {
            font-size: 3.5rem;
            font-family: v.$fuente_heading;
            color: v.$cianOscuro;
        }
        .antiguo {
            text-decoration: line-through;
        }

    }

    .boton {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 1rem;
        margin: 0;
        background-color: v.$cianOscuro;
        padding: 1.5rem;
        border-radius: .7rem;

        a {
            color: v.$blanco;
            font-size: 1.4rem;
        }

        &:hover {
            background-color: darken(v.$cianOscuro, 15%);
            cursor: pointer;
        }
    }
}