@use 'base/variables' as v;
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

*, 
*::after, 
*::before {
    box-sizing: inherit;
}

body {
    min-height: 100%;
    font-size: 1.6rem;
    font-family: v.$fuente_principal;
    background-color: v.$nata;
}

.contenedor_flex {
    display: flex;
    min-height: 100vh;
    align-items: center;
    padding: 5rem 0;

    @include m.tablet{
        padding: 0;
    }
}

.contenedor {
    width: 90%;
    max-width: 600px;
    margin: 0 auto;
    background-color: v.$blanco;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}