html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  min-height: 100%;
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
  background-color: hsl(30deg, 38%, 92%);
}

.contenedor_flex {
  display: flex;
  min-height: 100vh;
  align-items: center;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .contenedor_flex {
    padding: 0;
  }
}

.contenedor {
  width: 90%;
  max-width: 600px;
  margin: 0 auto;
  background-color: hsl(0deg, 0%, 100%);
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  text-align: center;
  font-weight: 700;
  margin: 0 0 1.6666666667rem 0;
  font-family: "Fraunces", serif;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.6rem;
  text-align: center;
}

p {
  font-size: 1.4rem;
  line-height: 2.5rem;
  color: hsl(228deg, 12%, 48%);
}

.contenedor_producto {
  border-radius: 0.8rem;
}
@media (min-width: 768px) {
  .contenedor_producto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor_producto .header_imagen img {
  border-radius: 0.8rem 0.8rem 0 0;
}
@media (min-width: 768px) {
  .contenedor_producto .header_imagen img {
    border-radius: 0.8rem 0 0 0.8rem;
    height: 100%;
  }
}

.producto {
  padding: 2rem;
}
@media (min-width: 768px) {
  .producto {
    padding: 3rem;
  }
}
.producto blockquote {
  margin: 0;
  margin-bottom: 1rem;
  text-transform: uppercase;
  color: hsl(228deg, 12%, 48%);
  font-size: 1.4rem;
  letter-spacing: 0.5rem;
}
@media (min-width: 768px) {
  .producto blockquote {
    margin-bottom: 2rem;
  }
}
.producto .titulo_producto {
  color: hsl(212deg, 21%, 14%);
  text-align: left;
  line-height: 3rem;
}
@media (min-width: 768px) {
  .producto .titulo_producto {
    font-size: 3.5rem;
    line-height: 4rem;
  }
}
.producto .precio_producto {
  display: flex;
  gap: 3rem;
  margin: 2rem 0;
}
.producto .precio_producto p {
  margin: 0;
}
.producto .precio_producto .actual {
  font-size: 3.5rem;
  font-family: "Fraunces", serif;
  color: hsl(158deg, 36%, 37%);
}
.producto .precio_producto .antiguo {
  text-decoration: line-through;
}
.producto .boton {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 0;
  background-color: hsl(158deg, 36%, 37%);
  padding: 1.5rem;
  border-radius: 0.7rem;
}
.producto .boton a {
  color: hsl(0deg, 0%, 100%);
  font-size: 1.4rem;
}
.producto .boton:hover {
  background-color: #244c3d;
  cursor: pointer;
}